.navbar{
	position: fixed;
	width: 100%;
	top: 0;
	background-color: #222222;
}
.navbar-title,.navbar-contents{
	position: relative;
	display: inline-block;
	text-align: center;
	vertical-align: top;
}
.navbar-title{
	width: 25%;
}
.navbar-contents{
	width: calc(100% / 4);
}
.navbar-text,.navbar-title-img{
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 60px;
	line-height: 1;
	color: white;
}
.navbar-title-img{
	margin: auto;
}
.navbar-title-text{
	font-size: 1.5vmax;
}
.navbar-text{
	font-size: 2.3vmax;
}