.main{
	padding-top: 60px;
}
.main-container{
	margin: 0 auto;
	width: 95%;
}
.main-logo{
	width: 100%;
}
.twitter-embed{
	float: right;
	width: 25%;
}
[id*="twitter-widget-"]{
	display: block;
	width: 100%;
	min-width: 0 !important;
}
.main-contents{
	display: block;
}
.contents,.top-contents{
	padding: 10px 20px;
}
.top-contents{
	font-family: "Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;
}
.contents{
	margin-bottom: 30px;
	background-color: white;
	color: black;
	box-shadow: 0px 0px 3px;
	border-radius: 4px;
}
.pending-contents{
	color: white;
	font-size: 2rem;
	text-align: center;
	padding: 50px 50px;
}
.contents-half{
	display: inline-block;
	width: calc((100% - 100px) / 2);
	vertical-align: top;
}
.contents-left{
	margin-right: 20px;
}
.description-title, .navbar-text, .contents-title{
	font-family: "Sanshita Swashed", cursive;
}
.description-title{
	color: white;
	font-size: 50px;
}
.description{
	font-family: "Hiragino Mincho ProN";
	font-weight: 400;
	color: white;
	font-size: 1.4rem;
}
.news-article{
	border-top: 1px dashed;
	padding: 10px;
}